import React, {useState} from "react"
import "./OrderList.css"
import {useRequest} from "ahooks"
import {Button, Descriptions, message, Modal, Table, Timeline} from "antd"
import {ClockCircleOutlined, ReloadOutlined} from "@ant-design/icons"

// helper functions
const truncateID = id => id.substring(id.length - 16)

const formatUTCMillis = timeMillis => (
  new Date(timeMillis)
    .toISOString()
    .replace(/T/, " ")
    .replace(/\..+/, "")
)

export default function OrderList() {
  // 
  const [deli, setDeli] = useState({visible: false, data: null})

  // 
  const [ord, setOrd] = useState({visible: false, data: null})

  // 
  const loadOrders = async () => {
    return await fetch(
      "http://localhost:8080/api/orders",
      {
        method: "GET"
      }
    ).then(response => {
      if (response.ok) {
        return response.json()
      } else {
        throw new Error("Failed to load orders!")
      }
    })
  }
  
  // 
  const getOrderDetail = async orderId => {
    return fetch(
      `http://localhost:8080/api/orders/${orderId}`,
      {
        method: "GET"
      }
    ).then(response => {
      if (response.ok) {
        return response.json()
      } else {
        throw new Error("Failed to get order detail!")
      }
    }).catch(error => {
      message.error(error.message)
    })
  }

  // 
  const getDeliveryDetail = async orderId => {
    return fetch(
      `http://localhost:8080/api/delivery?orderId=${orderId}`,
      {
        method: "GET"
      }
    ).then(response => {
      if (response.ok) {
        return response.json()
      } else {
        throw new Error("Failed to get delivery status!")
      }
    }).catch(error => {
      message.error(error.message)
    })
  }

  const {data, error, loading, runAsync} = useRequest(loadOrders)

  if (error) {
    message.error(error.message)
  }

  // 
  const columns = [
    {
      title: "",
      dataIndex: "orderId",
      key: "orderId",
      render: orderId => <span>{truncateID(orderId)}</span>
    },
    {
      title: "",
      dataIndex: "total",
      key: "total",
      render: total => <span>{"" + total.toFixed(1)}</span>
    },
    {
      title: "",
      dataIndex: "createdTime",
      key: "createdTime",
      render: timeMillis => (<span>{formatUTCMillis(timeMillis)}</span>)
    },
    {
      title: "",
      dataIndex: "orderStatus",
      key: "orderStatus",
      render: orderStatus => {
        const color = orderStatus === "" ? "#0abf5b" : "volcano"
        return (
          <span style={{color}}>{orderStatus}</span>
        )
      }
    },
    {
      title: "",
      key: "operation",
      render: (_, outline) => (
        <div>
          <Button
            type="link"
            style={{padding: 0, height: "100%", marginRight: "20px"}}
            onClick={() => {
              getOrderDetail(outline.orderId).then(data => {
                setOrd({...ord, visible: true, data})
              })
            }}
          >
            
          </Button>
          <Button
            type="link"
            style={{padding: 0, height: "100%"}}
            onClick={() => {
              getDeliveryDetail(outline.orderId).then(data => {
                setDeli({...deli, visible: true, data})
              })
            }}
          >
            
          </Button>
        </div>
      )
    }
  ]

  return (
    <div className="order-list">
      <Table
        title={() => (
          <div>
            <span className="order-list-title"></span>
            <Button
              shape="circle"
              style={{marginLeft: "10px"}}
              onClick={runAsync}
              icon={<ReloadOutlined style={{fontSize: "18px"}}/>}
            />
          </div>
        )}
        dataSource={data}
        columns={columns}
        loading={loading}
        rowKey={order => order.orderId}
      />
      <OrderDetail
        ord={ord}
        setOrd={setOrd}
      />
      <DeliveryDetail
        deli={deli}
        setDeli={setDeli}
      />
    </div>
  )
}

function OrderDetail(props) {

  const {ord, setOrd} = props

  const {visible, data} = ord

  return (
    <div className="order-detail">
      {
        visible &&
        <Modal
          width={600}
          title={<span></span>}
          centered={true}
          footer={null}
          visible={true}
          onCancel={() => setOrd({...ord, visible: false})}
        >
          <Descriptions layout="vertical" bordered={true}>
            <Descriptions.Item label="">{truncateID(data.orderId)}</Descriptions.Item>
            <Descriptions.Item label="">{formatUTCMillis(data.createdTime)}</Descriptions.Item>
            <Descriptions.Item label="">{"" + data.total.toFixed(2)}</Descriptions.Item>
            <Descriptions.Item label="">{data.orderStatus}</Descriptions.Item>
            <Descriptions.Item label="" span={2}>{formatUTCMillis(data.payedTime)}</Descriptions.Item>
            <Descriptions.Item label="">
              {
                data.items.map(item => (
                  <div className="order-detail-item" key={item.id}>
                    <div className="item-image-name">
                      <div className="item-image">
                        <img
                          alt={item.image}
                          src={item.image}
                          style={{width: "100%", height: "100%"}}
                        />
                      </div>
                      <div className="item-name">{item.name}</div>
                    </div>
                    <div className="item-price-quantity">
                      <span className="item-price">: {"" + item.price.toFixed(2)}</span>
                      <span className="item-quantity">: {item.quantity}</span>
                    </div>
                  </div>
                ))
              }
            </Descriptions.Item>
          </Descriptions>
        </Modal>
      }
    </div>
  )
}

function DeliveryDetail(props) {

  const {deli, setDeli} = props

  const {visible, data} = deli

  return (
    <div className="delivery-detail">
      {
        visible &&
        <Modal
          width={600}
          title={
            <div className="modal-header">
              <div className="delivery-detail-title"></div>
              <div className="delivery-header-info">{truncateID(data.orderId)}</div>
              <div className="delivery-header-info">{truncateID(data.deliveryId)}</div>
              <div className="delivery-header-info">{data.carrier}</div>
            </div>
          }
          centered={true}
          footer={null}
          visible={true}
          onCancel={() => setDeli({...deli, visible: false})}
        >
          <div className="delivery-timeline">
            <Timeline mode="left" style={{width: "500px"}}>
              {
                data.phases.map(p => (
                  <Timeline.Item
                    key={p.phaseId}
                    label={
                      <div>
                        <ClockCircleOutlined style={{fontSize: "16px", color: "#1890ff", marginRight: "6px"}}/>
                        <span>{formatUTCMillis(p.timeStamp)}</span>
                      </div>
                    }
                    color="green"
                  >
                    {p.message}
                  </Timeline.Item>
                ))
              }
            </Timeline>
          </div>
        </Modal>
      }
    </div>
  )
}
